.expedition-container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  padding: 0;
  margin: 0;
}

// 헤더와 메인 스타일 제거됨 (헤더 삭제했으므로)


@media (max-width: 768px) {
  .expedition-header {
    .header-content {
      flex-direction: column;
      gap: 20px;
    }

    .title-section {
      text-align: center;

      h1 {
        font-size: 2rem;
      }
    }

    .user-section {
      flex-direction: column;
      gap: 10px;
    }
  }

  .expedition-main {
    padding: 20px;
  }
}
